body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #96E5FF !important;
  margin: 0;
}

html, body, div#root, section.sc-title {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

section {
  position: relative;
  font-family: Quicksand;
  z-index: 2;
}

section .bg-img {
  position: absolute;
  top: 0;
  height: 100%;
  z-index: 2;
}

section .bg-img-first {
  left: 0;
  width: 100%;
  z-index: -1;
}

section .bg-img-second {
  left: 0;
  width: 100%;
  z-index: -1;
}

section .bg-img-third {
  z-index: -1;
}

section .bg-img-fourth {
  right: 0;
  z-index: -1;
}

div.container-fluid {
  padding: 0 9%;
}

section.sc-title {
  padding-top: 7%;
  z-index: 3;
  height: 100%;
}

.navbar {
  color: #fff;
  background: -webkit-linear-gradient(309.43deg, #3B5EF8 -1.91%, #00F3FF 162.61%);
  background: linear-gradient(140.57deg, #3B5EF8 -1.91%, #00F3FF 162.61%);
  z-index: 1000;
}

.custom-navbar {
  padding: 1% 9% !important;
}

.navbar-dark .navbar-nav .nav-item {
  padding: 0 18px;
}

.navbar-dark .navbar-nav .nav-item .dropdown button {
  background: #fff;
  color: #3b5ef8;
  border: none;
  outline: none;
}

.navbar-dark .navbar-nav a.nav-link {
  color: white;
}

section.sc-title div.div-intro {
  color: #fff;
  padding: 8% 0;
  padding-right: 7%;
  font-family: Quicksand;
}

section.sc-title div.div-intro h1 {
  font-size: 50px;
  font-weight: 900;
}

section.sc-title div.div-intro p {
  font-size: 24px;
}

section.sc-title div.div-intro-img {
  padding: 7% 0;
  margin-left: auto;
}

section.sc-title div.div-intro-img img {
  width: 100%;
  height: 100%;
}




/* Video section style */

section.sc-video {
  -webkit-transform: translateY(-11%);
          transform: translateY(-11%);
  padding-bottom: 4%;
}

section.sc-video div.div-video-description {
    color: #212121;
    padding: 7% 0;
    padding-right: 7%;
    padding-bottom: 10%;
}

section.sc-video div.div-video-description h2 {
  font-size: 2.25rem;
}

section.sc-video div.div-video-description p {
  padding-right: 13%;
  font-size: 1.125rem;
}

section.sc-video div.div-video-youtube {
  padding: 12% 0;
  padding-right: 7%;
  border-radius: 15px;
  height: 320px;
}

section.sc-video div.div-video-youtube .youtube-wrapper {
  position: relative;
  left: 5%;
}

section.sc-video div.div-video-youtube .youtube-wrapper .div-round-img {
  position: absolute;
  top: -22%;
  z-index: -1;
}


/* Service style */
section.sc-service {
  padding: 7% 0;
}

section.sc-service div.div-service-description {
  color: #111;
  padding-right: 5%;
  color: #fff;
}

section.sc-service div.div-service-description h2 {
  font-size: 2.25rem;
  font-weight: bold;
}

section.sc-service div.div-service-description p {
  font-size: 1.125rem;
}

section.sc-service div.service-card {
  padding: 19px 20px 10px;
  box-shadow: 0px 15px 15px rgba(59, 94, 248, 0.25);
}

section.sc-service div.service-card h5.card-title {
  font-size: 24px;
  color: #212121;
  font-weight: bold;
}

section.sc-service div.service-card p.card-text {
  font-size: 14px;
  line-height: 24px;
  color: #212121;
  padding-right: 5%;
}

section.sc-service div.service-card hr {
  margin: 20px 0;
  border-color: rgba(59, 94, 248, 0.25);
}

section.sc-service div.service-card ul.service-item-list {
  -webkit-padding-start: 0%;
          padding-inline-start: 0%;
}


section.sc-service div.service-card li.service-item {
  border: none;
  font-size: 18px;
  line-height: 1.25rem;
  padding-left: 0;
}



/* Work Customer Style */
section.sc-work-customer {
  padding-bottom: 7%;
}

div.customer-title h2{
  font-size: 2.25rem;
  line-height: 45px;
  font-weight: bold;
  text-align: center;
}

div.customer-content {
  margin-top: 6%;
} 

section.sc-work-customer {
  padding-top: 6%;
  background: #96e5ff
}

section.sc-work-customer div.customer-content div.customer-card {
  box-shadow: 0px 15px 15px rgba(59, 94, 248, 0.25);
  border-radius: 4px;
  border: none;
}

section.sc-work-customer div.customer-content div.customer-card img {
  width: 100%;
  /* height: 276px; */
}

section.sc-work-customer div.customer-content div.customer-card .card-body .card-text {
  font-size: 1.125rem;
  line-height: 22px;
  font-weight: bold;
  text-align: center;
}

section.sc-work-customer div.customer-content div.customer-card .card-body a {
  display: block;
  font-size: 0.75rem;
  line-height: 140%;
  text-align: center;
}

  
/* Happy Customer Style */
section.sc-happy-customer {
  padding: 7% 0;
}

section.sc-happy-customer div.div-brand {
  margin-bottom: 3%;
}

section.sc-happy-customer div.div-brand img.brand-img {
  width: 100%;
  box-shadow: 0px 6px 6px rgba(36, 143, 251, 0.15);
  border-radius: 4px;
}


/* Faq Style */
section.sc-faq {
  padding: 9% 0;
  -webkit-transform: translateY(-4%);
          transform: translateY(-4%);
  z-index: 0;
}

section.sc-faq img.bg-img-fourth {
  top: 30%;
  height: 600px;
}

section.sc-faq div.faq-title h2 {
  font-size: 2.25rem;
  line-height: 45px;
  text-align: center;
  font-weight: bold;
  color: #fff;
}

section.sc-faq div#accordion {
  margin-top: 5%;
  padding: 0 9%;
}

section.sc-faq div#accordion div.card {
  box-shadow: 0px 25px 45px rgba(36, 143, 251, 0.15);
  border-radius: 6px;
  margin-bottom: 20px;
}

section.sc-faq div#accordion div.card .card-header {
  background: #fff;
  padding: 25px 47px;
  box-shadow: 0px 5px 10px #E0E0E0;
}

section.sc-faq div#accordion div.card .card-header a.card-link {
  font-size: 1.125rem;
  font-weight: bold;
  line-height: 30px;
  text-decoration: none;
  color: #616161;
}

section.sc-faq div#accordion div.card .card-header a.card-link i {
  float: right;
  position: relative;
  top: 9px;
}

section.sc-faq div#accordion div.card .card-body {
  padding: 48px 38px;
  font-size: 1.125rem;
  line-height: 30px;
  color: #616161;
}



/* Contact Style */
section.sc-contact {
  padding: 8% 0;
  padding-bottom: 10%;
}

section.sc-contact .bg-img-first {
  -webkit-transform: scaleY(1.3);
          transform: scaleY(1.3);
}

section.sc-contact div.contact-header {
  padding-right: 30%;
}

section.sc-contact div.contact-header h2 {
  font-size: 2.25rem;
  font-weight: bold;
  line-height: 45px;
  color: #212121;
}

section.sc-contact div.contact-header p {
  padding-top: 11px;
  font-size: 1.125rem;
  line-height: 140%;
  color: #000;
}

section.sc-contact form.form-contact {
  margin-top: 11%;
}

section.sc-contact form.form-contact div {
  margin-bottom: 13px;
  margin-left: 0;
}

section.sc-contact form.form-contact input, section.sc-contact form.form-contact textarea {
  border-radius: 6px;
  padding: 25px;
  font-size: 1rem;
  line-height: 20px;
}

section.sc-contact form.form-contact button {
  background: #3b5ef8;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  width: 111px;
  height: 41px;
  font-size: 14px;
  line-height: 17px;
  color: #def3fe;
}



/* Footer Style */
section.sc-footer {
  background: #3B5EF8;
  color: #fff;
  padding-top: 15%;
  padding-bottom: 1%;
  z-index: 0;
}
section.sc-footer div.div-store p {
  margin-top: 18px;
  margin-bottom: 40px;
}

section.sc-footer div.div-store img {
  width: 120px;
  height: 38px;
}

section.sc-footer h3 {
  font-size: 18px;
  line-height: 22px;
  font-weight: bold;
}

section.sc-footer div.div-footer-country {
  font-size: 0.875rem;
  font-weight: bold;
}

section.sc-footer div.div-footer-country img {
  width: 17px;
  height: 12px;
}

section.sc-footer div.footer-item {
  font-size: 0.875rem;
  line-height: 150%;
}

div.div-subscribe .col-8 {
  padding-right: 10px;
}

div.div-subscribe input {
  padding: 20.5px 17px;
}

div.div-subscribe input, div.div-subscribe button {
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 17px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
}

div.div-subscribe button {
  background: #26F5FF;
  color: #111;
  border: none;
  outline: none;
  padding: 13px 17px;
}

p.copyright {
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  padding-top: 1%;
}

















/* Miscells */
.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
